#popup {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 9;
    background: rgba(0,0,0,.75);
    -webkit-transition: all 0.3s cubic-bezier(.25,.8,.25,1);
    -o-transition: all 0.3s cubic-bezier(.25,.8,.25,1);
    transition: all 0.3s cubic-bezier(.25,.8,.25,1);
}

#popup .bg {
    position: absolute;
    top: 10%;
    bottom: 10%;
    left: 10%;
    right: 10%;
    background: #111111;
    -webkit-box-shadow: 0 10px 20px rgba(0,0,0,0.12), 0 5px 5px rgba(0,0,0,0.24);
            box-shadow: 0 10px 20px rgba(0,0,0,0.12), 0 5px 5px rgba(0,0,0,0.24);
    z-index: 8;
    border: 15px solid #FF6F00;
}

#popup .content {
    position: relative;
    z-index: 9;
    text-shadow: 5px 5px 5px #000;
}

@-webkit-keyframes attractor {
    0% {-webkit-transform: scale(1) rotate(0);transform: scale(1) rotate(0);}
    25% {-webkit-transform: scale(1.25) rotate(-2deg);transform: scale(1.25) rotate(-2deg);}
    50% {-webkit-transform: scale(1);transform: scale(1);}
    75% {-webkit-transform: scale(1.25) rotate(2deg);transform: scale(1.25) rotate(2deg);}
    100% {-webkit-transform: scale(1) rotate(0);transform: scale(1) rotate(0);}
}

@keyframes attractor {
    0% {-webkit-transform: scale(1) rotate(0);transform: scale(1) rotate(0);}
    25% {-webkit-transform: scale(1.25) rotate(-2deg);transform: scale(1.25) rotate(-2deg);}
    50% {-webkit-transform: scale(1);transform: scale(1);}
    75% {-webkit-transform: scale(1.25) rotate(2deg);transform: scale(1.25) rotate(2deg);}
    100% {-webkit-transform: scale(1) rotate(0);transform: scale(1) rotate(0);}
}

@-webkit-keyframes hide {
    0% {-webkit-transform: scale(1);transform: scale(1); opacity: 1;}
    25% {-webkit-transform: scale(1.1);transform: scale(1.1); opacity: 1; background: transparent;}
    100% {-webkit-transform: scale(.75);transform: scale(.75); opacity: 0; background: transparent;}
}

@keyframes hide {
    0% {-webkit-transform: scale(1);transform: scale(1); opacity: 1;}
    25% {-webkit-transform: scale(1.1);transform: scale(1.1); opacity: 1; background: transparent;}
    100% {-webkit-transform: scale(.75);transform: scale(.75); opacity: 0; background: transparent;}
}
@-webkit-keyframes show {
    0% {-webkit-transform: scale(.75);transform: scale(.75); opacity: 0; background: transparent;}
    25% {-webkit-transform: scale(1.1);transform: scale(1.1); opacity: 1; background: transparent;}
    50% {-webkit-transform: scale(1);transform: scale(1); opacity: 1}
}
@keyframes show {
    0% {-webkit-transform: scale(.75);transform: scale(.75); opacity: 0; background: transparent;}
    25% {-webkit-transform: scale(1.1);transform: scale(1.1); opacity: 1; background: transparent;}
    50% {-webkit-transform: scale(1);transform: scale(1); opacity: 1}
}


