.label-container {
    text-align: center;
    z-index: 1;
    position: relative;
}

.label-container h1 {
    opacity: 0;
    text-transform: uppercase;
    color: #E65100;
    display: inline-block;
    padding: 20px;
}

@-webkit-keyframes pop {
    0% {-webkit-transform: scale(0);transform: scale(0); opacity: 0;}
    10% {-webkit-transform: scale(1);transform: scale(1); opacity: 1;}
    20% {-webkit-transform: scale(1.5);transform: scale(1.5);}
    40% {-webkit-transform: scale(1);transform: scale(1);}
    60% {-webkit-transform: scale(.5);transform: scale(.5);}
    80% {opacity: 1;}
    100% {-webkit-transform: scale(0);transform: scale(0); opacity: 0;}
}

@keyframes pop {
    0% {-webkit-transform: scale(0);transform: scale(0); opacity: 0;}
    10% {-webkit-transform: scale(1);transform: scale(1); opacity: 1;}
    20% {-webkit-transform: scale(1.5);transform: scale(1.5);}
    40% {-webkit-transform: scale(1);transform: scale(1);}
    60% {-webkit-transform: scale(.5);transform: scale(.5);}
    80% {opacity: 1;}
    100% {-webkit-transform: scale(0);transform: scale(0); opacity: 0;}
}

@-webkit-keyframes pop-long {
    0% {-webkit-transform: scale(0);transform: scale(0); opacity: 0;}
    5% {-webkit-transform: scale(1) rotate(0);transform: scale(1) rotate(0); opacity: 1;}
    10% {-webkit-transform: scale(1.5);transform: scale(1.5);}
    20% {-webkit-transform: scale(1);transform: scale(1);}
    30% {-webkit-transform: rotate(5deg);transform: rotate(5deg);}
    35% {-webkit-transform: rotate(-5deg);transform: rotate(-5deg);}
    40% {-webkit-transform: rotate(5deg);transform: rotate(5deg);}
    45% {-webkit-transform: rotate(-5deg);transform: rotate(-5deg);}
    50% {-webkit-transform: rotate(5deg) scale(1);transform: rotate(5deg) scale(1);}
    55% {-webkit-transform: rotate(-5deg);transform: rotate(-5deg);}
    60% {-webkit-transform: rotate(5deg);transform: rotate(5deg);}
    65% {-webkit-transform: rotate(-5deg);transform: rotate(-5deg);}
    70% {-webkit-transform: rotate(5deg);transform: rotate(5deg);}
    75% {-webkit-transform: rotate(-5deg);transform: rotate(-5deg);}
    80% {-webkit-transform: scale(1) rotate(0);transform: scale(1) rotate(0); opacity: 1;}
    100% {-webkit-transform: scale(0);transform: scale(0); opacity: 0;}
}

@keyframes pop-long {
    0% {-webkit-transform: scale(0);transform: scale(0); opacity: 0;}
    5% {-webkit-transform: scale(1) rotate(0);transform: scale(1) rotate(0); opacity: 1;}
    10% {-webkit-transform: scale(1.5);transform: scale(1.5);}
    20% {-webkit-transform: scale(1);transform: scale(1);}
    30% {-webkit-transform: rotate(5deg);transform: rotate(5deg);}
    35% {-webkit-transform: rotate(-5deg);transform: rotate(-5deg);}
    40% {-webkit-transform: rotate(5deg);transform: rotate(5deg);}
    45% {-webkit-transform: rotate(-5deg);transform: rotate(-5deg);}
    50% {-webkit-transform: rotate(5deg) scale(1);transform: rotate(5deg) scale(1);}
    55% {-webkit-transform: rotate(-5deg);transform: rotate(-5deg);}
    60% {-webkit-transform: rotate(5deg);transform: rotate(5deg);}
    65% {-webkit-transform: rotate(-5deg);transform: rotate(-5deg);}
    70% {-webkit-transform: rotate(5deg);transform: rotate(5deg);}
    75% {-webkit-transform: rotate(-5deg);transform: rotate(-5deg);}
    80% {-webkit-transform: scale(1) rotate(0);transform: scale(1) rotate(0); opacity: 1;}
    100% {-webkit-transform: scale(0);transform: scale(0); opacity: 0;}
}


